import moxios from 'moxios'
import * as mocks from './picturedisplay/mocks/tags'

export default () => {
  moxios.install()
  moxios.stubRequest('/api/all-tags', {
    status: 200,
    response: {
      'tags': [
        mocks.newTagJson,
        mocks.newTagJson2
      ]
    }
  })
}



